<style lang="less">
   @import './docment.less';
</style>

<template>
   <div class="panel" @keydown.enter="doQuery">
      <div class="doc-title">
         <div class="doc-tile-left">
            <h1>welcome to {{ docName }}</h1>
         </div>
         <div class="doc-title-right">
            <Input class="query-input" v-model="queryContent" placeholder="please enter info what you want to search"
                   :on-enter="doQuery"></Input>
            <Button type="primary" icon="ios-search" v-on:click="doQuery">Search</Button>
            <Button type="primary" icon="close-circled" v-on:click="doClear">Clear</Button>
         </div>
      </div>
      <div class="document-content">
         <div class="url-title">
            <h1>API documents</h1>
         </div>
         <div class="content-panel" v-for="(doc,index) in docs">
            <div class="data-panel" v-on:click="showOrHide(doc)">
                  <h2>{{index+1}}、{{doc.Url}}</h2>
            </div>
            <div class="params-panel" v-if="panelSeenReq[doc.Id]"><!-- v-if="panelSeenReq[doc.Id]"-->
               <div style="margin-top: 10px;">
                  <div class="params-header">
                     <div class="header-one"><p>Body</p></div>
                    <!-- <div class="header-two" v-show="editeShow[doc.Id]">
                        <a href="javascript:void(0)" class="try-button" v-on:click="tryIt(doc)">try it out</a>
                     </div>-->
                  </div>
                  <div class="description-panel" v-if="isNotNull(doc.Description)">
                     <h4><span style="font-size: 18px;">Description : </span><span style="font-size: 16px">{{doc.Description}}</span></h4>
                  </div>
                  <div class="params-form" v-show="editeShow[doc.Id]">
                     <table style="margin-bottom: 20px;" class="params-table">
                        <thead>
                        <th>Field</th>
                        <th>Value</th>
                        </thead>
                     </table>
                     <div class="newForm-panel" v-for="(value,k) in doc.Body">
                        <div class="leftItems">{{k}}<i class="required-lable" v-if="checkReq(value)">*</i></div>
                        <div class="rightItems">
                           <div v-if="judgeType(value)">
                              <Input v-model="formItem[doc.Id+k]"
                                     v-bind:placeholder="'please enter '+k+' value'"
                                     class="form-input" @on-blur="validate(value,k,doc.Id)"></Input>
                              <Button type="primary" size="small" v-if="cumputerLongTime(value)"
                                      v-on:click="setTimestamp(doc.Id+k)">timestamp
                              </Button>
                           </div>
                           <div v-else style="width: 100%">
                              <form_panel :child-data="childData[doc.Id + k]"></form_panel>
                           </div>
                        </div>
                     </div>
                  </div>
                  <div class="params-form" v-show="!editeShow[doc.Id]">
                     <json-editor v-model="bodyJson[doc.Id]"></json-editor>
                  </div>
                  <div class="respone-header" style="margin-bottom: 30px;" >
                     <div class="header-one">
                        <Button type="primary" v-on:click="doEdite(doc.Id)">{{editeShow[doc.Id]?"showJson":"Edite"}}</Button>
                        <Button style="margin-left: 20px;" type="error" v-on:click="tryIt(doc)" v-show="editeShow[doc.Id]">Try It Out</Button>
                     </div>
                  </div>
                  <!--<div class="respone-header" >
                     <div class="header-one"><p>Header</p></div>
                  </div>
                  <div class="resp-form" >
                     <div class="cm-container">
                        <json-editor v-model="rspCode[doc.Id]"></json-editor>
                     </div>
                  </div>-->

                  <div class="respone-header" v-show="editeShow[doc.Id]">
                     <div class="header-one"><p>Response</p></div>
                  </div>
                  <div class="resp-form" v-show="editeShow[doc.Id]">
                     <div class="cm-container">
                        <json-editor v-model="rspCode[doc.Id]"></json-editor>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
   </div>
</template>
<script>
   import enviroment from '../../libs/enviroment';
   import JsonEditor from './jsonEditor';
   import Form_panel from "./form_panel";
   // import resolver from '../../libs/extend';


   export default {
      components: {
         Form_panel,
         JsonEditor
      },
      props: ['value'],
      data() {
         return {
            webKey: '',
            pkPath: '',
            rspCode: {},
            docName: "",
            panelSeenReq: {},
            editeShow: {},
            reqDataFormat: {},
            rspDataFormat: {},
            formItem: {},
            bodyJson: {},
            docs: [],
            queryDocs: [],
            childData: {},
            data: {},
            docsKey: {},
            queryContent: '',
            baseUrl: '',
            coolRoot: {},
         };
      },
      methods: {
         coolDocument() {
            var url = "/coolDocument/createDocument";
            // var url = "/business/position/document";
            this.axios(url, {
               method: "POST",
               baseURL: this.baseUrl,
               type: "json"
            }).then(res => {
               var data = res.data;
               this.initProtoBuf(data.rows.ProtoFile)
               this.docName = data.docName;
               this.webKey = data.docKey;
               this.pkPath = data.docPkPath;
               this.docs = [];

               var rows = data.rows.Ants;
               for(var k in rows){
                  var rowData = rows[k];
                  var body = rowData.Body;
                  if (rowData.Url.indexOf(this.queryContent) > -1) {
                     this.initForm(rowData);
                     this.initBodyJson(rowData);
                     this.$set(this.docsKey, rowData["Id"], {});
                     this.docsKey[rowData["Id"]] = rowData
                     // this.checkDoc(rowData, data.docPkPath);
                     this.docs.push(rowData);
                     this.filedHandler(body, rowData["Id"], "");
                     this.$set(this.panelSeenReq, rowData["Id"], false);
                     this.$set(this.rspCode, rowData["Id"], '');
                  }
               }
            });
         },
         initBodyJson(doc) {
            this.$set(this.editeShow, doc.Id, false);
            var json = {};
            this.dealJson(doc.Body, json);
            this.$set(this.bodyJson, doc.Id, json);
         },
         dealJson(rowData, json) {
            for (var key in rowData) {
               if (typeof rowData[key] == "object") {
                  var nextJson = {};
                  this.dealJson(rowData[key],nextJson);
                  json[key] = nextJson;//
                  continue;
               }
               let value = rowData[key].split(",");
               json[key] = value[0];
            }
         },
         initForm(rowData) {
            var body = rowData.Body;
            for (var key in body) {
               if (typeof body[key] === "object") {
                  this.$set(this.childData, rowData.Id + key, {});
                  this.childData[rowData.Id + key].data = body[key];
                  this.childData[rowData.Id + key].key = key;
                  this.childData[rowData.Id + key].Id = rowData.Id;
                  this.childData[rowData.Id + key].formItem = this.formItem;
               }
            }
         },
         initProtoBuf(data) {
            for (var key in data) {
               var protobuf = data[key];
               this.realInit(protobuf);
            }
         },
         realInit(protoFile) {
            var random = Math.random().toString(36).substr(2);
            this.protobuf.load("/static/proto/" + protoFile + "?" + random, (err, root) => {
               if (err) {
                  this.shoMsg('找不到' + protoFile + '文件', 0)
                  return
               }
               this.$set(this.coolRoot, protoFile, root);
            });
         },
         doEdite(id) {
            this.editeShow[id] = !this.editeShow[id];
         },
         setTimestamp(key) {
            this.formItem[key] = new Date().getTime();
         },
         cumputerLongTime(type) {
            var valStr = type.split(",");
            if (valStr[0] === "int64") {
               return true;
            }
            return false;
         },
         filedHandler(object, id, readykey) {
            for (var key in object) {
               if (typeof object[key] === "object") {
                  this.filedHandler(object[key], id, readykey + key);
                  continue;
               }
              /* if (key === "UserId") {
                  this.$set(this.formItem, id + readykey + key, 'a1f5888e50b3a78');
                  continue;
               }
               if (key === "WatchId") {
                  this.$set(this.formItem, id + readykey + key, 'w3c0f7afbe78cbb');
                  continue;
               }*/
               this.$set(this.formItem, id + readykey + key, '');
            }
         },
         showOrHide(doc) {
            this.panelSeenReq[doc.Id] = !this.panelSeenReq[doc.Id];
         },
         getInputType(fieldType) {
            if (fieldType === "string") {
               return false;
            }
            if (fieldType === "int32" || fieldType === "float64" || fieldType === "int64" || fieldType === "double") {
               return true;
            }
         },
         judgeType(value) {
            return typeof value !== "object";
         },
         tryIt(doc) {

            this.rspCode[doc.Id] = "";
            this.executeprotoBuf(doc);
         },
         validate(valueStr, key, id) {
            let value = valueStr.split(",");
            var v = this.formItem[id + key];
            if (v.trim() === "") {
               this.formItem[id + key] = "";
               return;
            }
            if (this.getInputType(value[0]) && !this.isNum(v)) {
               this.formItem[id + key] = "";
               this.shoMsg(key + '字段必须是数字', 0)
            }
         },
         isNum(val) {
            if (val === "" || val === null) {
               return false;
            }
            if (!isNaN(val)) {
               return true;
            } else {
               return false;
            }
         },
         executeprotoBuf(doc) {

            let vue = this;
            let root = this.coolRoot[doc.ProtoBufFileName]
            let protBufCtr = vue.pkPath + "." + doc.ProtoBufControl;
            let payload = {};
            try {
               console.log("获取当前的数据集合:",protBufCtr);
               var reqData = root.lookupType(protBufCtr);
               this.valueStruct(reqData, doc, payload, '', doc.ProtoBufFileName);
               console.log("show request data:", payload);
               this.doRequest(reqData, payload, doc, root);
            } catch (err) {
               console.error(err);
               // this.shoMsg(err)
               this.rspCode[doc.Id] = err;
            }
         },
         valueStruct(reqData, doc, payload, chainUpField, protoFile) {
            var id = doc.Id;
            var fields = reqData.fieldsArray;
            var root = this.coolRoot[protoFile];

            for (var i = 0; i < fields.length; i++) {
               var rowData = fields[i];

               var cntChain = chainUpField + this.headToUpcase(rowData.name);
               var bytePlugin = this.getPlugin(reqData, rowData.name, doc.Url);
               var plugin = this.getPlugin(reqData, rowData.name, doc.Url);
               var resolver = this.resolver();

               // var resolver = window.Resolver;
               resolver.byteInvoke(bytePlugin);
               if (bytePlugin.swapStruct !== '' && rowData.type === 'bytes') {
                  var retData = this.getPluginField(doc, bytePlugin, cntChain, root, protoFile)
                  var msgData = retData.data;
                  var retpayload = retData.payload;
                  var errMsg = msgData.verify(retpayload);

                  if (errMsg) {
                     this.$Spin.hide();
                     throw Error(errMsg);
                  }
                  var message = msgData.create(retpayload);
                  var buffer = msgData.encode(message).finish();
                  payload[rowData.name] = Uint8Array.from(buffer);
                  continue;
               }
               resolver.dataInvoke(plugin);
               if (plugin.swapStruct !== '' && rowData.type !== 'bytes') {
                  var retData = this.getPluginField(doc, plugin, cntChain, root, protoFile)
                  payload[rowData.name] = retData.payload;
                  continue;
               }
               if (!this.isBaseType(rowData.type)) {

                  var data = root.lookupType(this.pkPath + "." + rowData.type);
                  var nextPayLoad = {};
                  this.valueStruct(data, doc, nextPayLoad, cntChain, protoFile)
                  var errMsg = data.verify(nextPayLoad);
                  if (errMsg) {
                     this.$Spin.hide();
                     throw Error(errMsg);
                  }
                  payload[rowData.name] = nextPayLoad;
                  continue;
               }
               var key = id + chainUpField + this.headToUpcase(rowData.name)
               var value = this.formItem[key];


               if (rowData.required && !this.isNotNull(value + "")) {
                  this.$Message.warning(rowData.name + ' 是必填字段');
                  return;
               }
               if (this.getInputType(rowData.type)) {
                  value = this.parseFloat(value);
               }
               payload[rowData.name] = value;
            }
         },
         getPluginField(doc, plugin, upName, root, protoFile) {
            var id = doc.Id
            if (this.isNotNull(plugin.RowProtoFile)) {
               root = this.coolRoot[plugin.RowProtoFile];
               protoFile = plugin.RowProtoFile;
            }

            var data = root.lookupType(this.pkPath + '.' + plugin.swapStruct);

            var payload = {};
            var fields = data.fieldsArray;
            for (var i = 0; i < fields.length; i++) {
               var rowData = fields[i];
               if (!this.isBaseType(rowData.type)) {
                  var rtd = root.lookupType(this.pkPath + "." + rowData.type);

                  var rtpayload = {};
                  this.valueStruct(rtd, doc, rtpayload, this.headToUpcase(upName) + this.headToUpcase(rowData.name), protoFile);
                  payload[rowData.name] = rtpayload;
               } else {
                  var key = id + this.headToUpcase(upName) + this.headToUpcase(rowData.name)
                  var value = this.formItem[key];
                  if (rowData.required && !this.isNotNull(value + "")) {
                     this.$Message.warning(rowData.name + ' 是必填字段');
                     return;
                  }
                  if (this.getInputType(rowData.type)) {
                     value = this.parseFloat(value);
                  }
                  payload[rowData.name] = value;
               }
            }
            var retData = {
               payload: payload,
               data: data
            }
            return retData;
         },
         doRequest(reqData, payload, doc, root) {
            this.$Spin.show({
               render: (h) => {
                  return h('div', [
                     h('Icon', {
                        'class': 'demo-spin-icon-load',
                        props: {
                           type: 'load-c',
                           size: 18
                        }
                     }),
                     h('div', '请求中，请稍等...')
                  ]);
               }
            });
            var errMsg = reqData.verify(payload);

            if (errMsg) {
               this.$Spin.hide();
               this.rspCode[doc.Id] = errMsg;
               throw Error(errMsg);
            }
            var message = reqData.create(payload);
            var buffer = reqData.encode(message).finish();
            var header = enviroment.getRequestHeader(buffer, this.webKey);

            this.axios(doc.Url, {
               method: "POST",
               baseURL: this.baseUrl,
               timeout: 5000,
               responseType: 'arraybuffer',
               headers: {
                  'Content-type': 'application/x-protobuf',
                  'Sign': header.sign,
                  'Timestamp': header.timeStamp,
                  'length': buffer.length
               },
               data: Uint8Array.from(buffer)
            }).then(res => {
               console.log('获取返回结果:',res);
               var rspData;
               try {
                  rspData = root.lookupType(doc.RespProtoBufCtrl);
               } catch (err) {
                  this.shoMsg("找不到" + doc.ProtoBufFileName + "下对应的" + doc.RespProtoBufCtrl);
               }
               var buffer = new Uint8Array(res.data);
               if (buffer) {
                  var message = rspData.decode(buffer);
                  var respData = {};
                  this.analyzeResp(message, doc.RespProtoBufCtrl, doc.Url, root, respData);
                  this.rspCode[doc.Id] = respData;
                  this.$Spin.hide();
               }

            }).catch(err => {
               console.error(err);
               this.rspCode[doc.Id] = err;
               this.$Spin.hide();
            });
         },
         analyzeResp(message, protoCtrl, url, root, respData) {
            let msgKeys = Object.keys(message);
            var resolver = this.resolver();
            for (var i = 0; i < msgKeys.length; i++) {
               var reqData = {name: protoCtrl}
               let plugin = this.getPlugin(reqData, msgKeys[i], url)
               resolver.RspInvoke(plugin);
               if (this.isNotNull(plugin.swapStruct)) {
                  respData[msgKeys[i]] = this.RespController(plugin, plugin.swapStruct, url, root, message[msgKeys[i]]);
                  plugin.swapStruct = '';
                  continue;
               }
               respData[msgKeys[i]] = message[msgKeys[i]];
               console.log(msgKeys[i], message[msgKeys[i]]);
            }
            return respData;
         },
         RespController(plugin, protoCtrl, url, root, value) {
            let oldRoot = root;
            let ctrfile = protoCtrl
            if (this.isNotNull(plugin.RowProtoFile)) {
               root = this.coolRoot[plugin.RowProtoFile];
               ctrfile = plugin.RowProtoFile
            }
            var message;
            try {
               message = root.lookupType(plugin.swapStruct);
            } catch (err) {
               this.shoMsg("找不到" + ctrfile + "下对应的" + plugin.swapStruct);
            }
            var data = message.decode(value);
            var rpl = this.analyzeResp(data, protoCtrl, url, oldRoot, {});
            return rpl;
         },
         parseFloat(value) {
            if (this.isNotNull(value + '')) {
               return parseFloat(value);
            }
            return 0;
         },
         isBaseType(type) {
            var baseTypeArray = ["int32", "float64", "int64", "string", "double"];
            for (var i = 0; i < baseTypeArray.length; i++) {
               if (baseTypeArray[i] === type) {
                  return true;
               }
            }
            return false;
         },
         headToUpcase(str) {
            var len = str.length;
            var newStr = "";
            for (var i = 0; i < len; i++) {
               if (i === 0) {
                  newStr += str[i].toUpperCase();
               } else {
                  newStr += str[i];
               }
            }
            return newStr;
         },
         resolver() {
            // return resolver;
            return window.Resolver
         },
         doQuery() {
            this.coolDocument();
         },
         doClear() {
            this.queryContent = '';
            this.doQuery();
         },
         shoMsg(msg, dur) {
            this.$Message.warning({
               content: msg,
               duration: dur === 0 ? 0 : 2
            });
         },
         isNotNull(str) {
            if (str === undefined) {
               return false;
            }
            if (str.trim() === "") {
               return false;
            }
            return true;
         },
         checkReq(fieldInfo) {
            if (typeof fieldInfo == "object") {
               return true;
            }
            var fno = fieldInfo.split(",");
            if (fno[1] == "req") {
               return true;
            }
            return false;
         },
         getPlugin(reqData, fieldName, url) {
            var plugin = {
               StructureName: reqData.name,
               FieldName: fieldName,
               Url: url,
               type: reqData.type,
               swapStruct: '',
               RowProtoFile: '',
               Swap: function (retStrct, rp) {
                  this.swapStruct = retStrct
                  if (rp !== null) {
                     this.RowProtoFile = rp;
                  }
               },
               SwapFile: function (rp) {
                  if (rp !== null) {
                     this.RowProtoFile = rp;
                  }
               }
            }
            return plugin;
         }
      },
      created() {
         this.coolDocument();
      }
   };
</script>

<style>

</style>



